const main = document.querySelector('#main')
const filtro = document.querySelectorAll('#filtro li')
let cards
let time = 'weekly'
let dados = {}
const msgTime = {
  'daily': 'Yesterday',
  'weekly': 'Last Week',
  'monthly': 'Last Month'
}

// adc a função em cada botão do filtro
filtro.forEach(btn => {
  btn.addEventListener('click', handleClick)
})

fetch('./data.json')
  .then(r => r.json())
  .then(jsonDados => {
    jsonDados.forEach(dado => {
      // adc os cards novos logo após o card perfil
      main.insertAdjacentHTML('beforeend', criarCard(dado, time))
    })

    jsonDados.forEach(dado => {
      dados[dado.title] = dado.timeframes
    })

    cards = document.querySelectorAll('.card')
  })

  function handleClick(e) {
    filtro.forEach(btn => {
      btn.classList.remove('ativo')
    })
    e.target.classList.add('ativo')
    time = e.target.innerText.toLowerCase()

    atualizarCards(time)
  }

  function atualizarCards(time) {
    cards.forEach(card => {
      atualizarCard(card, time)
    })
  }

  function atualizarCard(card, time) {
    const titulo = card.querySelector('.titulo').innerText
    const atual = dados[titulo][time]['current']
    const anterior = dados[titulo][time]['previous']
    const cardHoras = card.querySelector('.hora')
    cardHoras.innerText = `${atual}`
    const timeDesc = card.querySelector('.desc')
    timeDesc.innerText = `${msgTime[time]} - ${anterior}hrs`
  }

  function criarCard(card, time) {
    return `<div class="card ${card['title'].toLowerCase().replace(/\s/g, '')}">
    <div class="card__info">
      <div class="info__title">
        <h4 class="titulo">${card['title']}</h4>
        <div class="dots">
          <img src="img/icon-ellipsis.svg" alt="">
        </div>
      </div>

      <div class="info__dados">
        <div class="time"><span class="hora">${card['timeframes'][time]['current']}</span>hrs</div>
        <p class="desc"><span>${msgTime[time]}</span> - ${card['timeframes'][time]['previous']}hrs</p>
      </div>
    </div>
  </div>`
  }
